import React, {useState} from 'react';
import ConsolePrinter from './ConsolePrinter';
import RandomWriter from './RandomWriter';

const CustomConsole = ({}) => {
    const [currentCMD, setCurrentCMD] = useState('');
    const handleClick = (e) => {
        setCurrentCMD(e.target.attributes.data.nodeValue);
    };

    return  <div>
                <div className="App-menu-container">
                    <span onClick={handleClick} data=" load PROJECTS" className="App-menu-option"> {'>'} PROJECTS</span> 
                    <span onClick={handleClick} data=" load EXPERIENCE" className="App-menu-option"> {'>'} EXPERIENCE</span>
                    <span onClick={handleClick} data=" load ABOUT_ME" className="App-menu-option"> {'>'} ABOUT ME</span>
                </div>
                <div className="custom-console">
                    <div className="custom-console-header">CONSOLE</div>
                    <div className="custom-console-body">
                        <span>guest@jrotter2.github.io ~ {'>'}</span>
                        <RandomWriter text={[currentCMD]} num_rolls={4} delay={5} text_delay={1000} infinite />
                        <ConsolePrinter currentCMD={currentCMD} init_delay={1000} delay={35} pause={4} infinite /> 
                    </div>
                </div>
            </div>;
};


export default CustomConsole;