import React, { useState, useEffect } from 'react';
import Tex2SVG from "react-hook-mathjax";
import {Plot, LineSeries, Legend, Axis, Heading} from 'react-plot';

const MCOptionPricer = ({ }) => {

    const [paths, setPaths] = useState([[{"x":0 , "y":0} ,{"x":50 , "y":50},{"x":100 , "y":100}]]);
    const [optionPrice, setOptionPrice] = useState("-");
    
    const [plotWidth, setPlotWidth] = useState(300);
    const [isMobile, setIsMobile] = useState(false);

    const [optionType, setOptionType] = useState(0);
    const [riskFreeRate, setRiskFreeRate] = useState(.03);
    const [optionPos, setOptionPos] = useState(0);
    const [s0, setS0] = useState(100);
    const [sMAX, setSMAX] = useState(100);
    const [sMIN, setSMIN] = useState(0);
    const [K, setK] = useState(101);
    const [mu, setMu] = useState(0);
    const [sigma, setSigma] = useState(.05);
    const [T, setT] = useState(10);
    const [deltaT, setDeltaT] = useState(.1);
    const [num_paths, setNumPaths] = useState(100);

    const getGuassianRandom = (mean, stdev) => {
        const u = 1 - Math.random(); // Converting [0,1) to (0,1]
        const v = Math.random();
        const z = Math.sqrt( -2.0 * Math.log( u ) ) * Math.cos( 2.0 * Math.PI * v );
        // Transform to the desired mean and standard deviation:
        return z * stdev + mean;
    }

    const getNewOptionPrice = () => {
        if(optionType == 0) // AMERICAN 
        {
            let avg_cost = 0
            for(let p = 0; p < paths.length; p++)
            {
                let max_price = 0;
                let time_at_max = 0;
                let time_at_min = 0;
                let min_price = 2*K;
                for(let i = 1; i < paths[p].length; i++)
                {
                    if(paths[p][i].y > max_price)
                    {
                        max_price = paths[p][i].y;
                        time_at_max = i;
                    }
                    if(paths[p][i].y < min_price)
                    {
                        min_price = paths[p][i].y;
                        time_at_min = i;
                    }
                }
                if(optionPos == 0) // CALL
                {
                    console.log(max_price, K, riskFreeRate, time_at_max, (Math.max(0,max_price - K)) * Math.exp(-riskFreeRate*time_at_max));
                    avg_cost+=(Math.max(0,max_price - K)) // * Math.exp(-riskFreeRate*time_at_max);
                }else{ // PUT
                    avg_cost+=(Math.max(0,K-min_price)) // * Math.exp(-riskFreeRate*time_at_min);
                }
            }
            let optionPrice = avg_cost/num_paths * Math.exp(-riskFreeRate * T);
            const formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
              });
            setOptionPrice(formatter.format(optionPrice));
        }else if (optionType == 1)
        {
            let avg_cost = 0
            for(let p = 0; p < paths.length; p++)
            {
                if(optionPos == 0) // CALL
                {
                    avg_cost+= Math.max(0,paths[p][paths[p].length-1].y - K);
                }else{ // PUT
                    avg_cost+= Math.max(0,K-paths[p][paths[p].length-1].y);
                }
            }
            let optionPrice = avg_cost/num_paths * Math.exp(-riskFreeRate * T);
            const formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
              });
            setOptionPrice(formatter.format(optionPrice));
        }else if (optionType == 2)
        {
            let avg_cost = 0
            for(let p = 0; p < paths.length; p++)
            {
                let avg_price = 0;
                for(let i = 1; i < paths[p].length; i++)
                {
                    avg_price +=  paths[p][i].y;
                }
                avg_price = avg_price / paths[p].length; 
                console.log(avg_price);
                if(optionPos == 0) // CALL
                {
                    avg_cost+=(Math.max(0,avg_price - K));
                }else{ // PUT
                    avg_cost+=(Math.max(0,K-avg_price));
                }
            }
            let optionPrice = avg_cost/num_paths * Math.exp(-riskFreeRate * T);
            const formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
              });
            setOptionPrice(formatter.format(optionPrice));
        }
    };

    const getNewPaths = () => {
        let max_S = 0;
        let currentIndex = 0;
        let min_S = -1;
        setPaths([]);
        let num_steps = parseInt(T / deltaT);

        let new_paths = []
        for(let p =0; p < num_paths; p++)
        {
            let path = [0];
            for(let i = 1; i <= num_steps; i++)
            {
                let stoch_path = deltaT * (riskFreeRate - .5 * sigma*sigma);
                let rand_path = sigma * getGuassianRandom(0, deltaT);
                path.push(path[i-1] + stoch_path + rand_path);
            }
            let final_path = [];
            for(let i = 0; i <= num_steps; i++)
            {
                if(s0 * Math.exp(path[i]) > max_S){
                    max_S = s0 * Math.exp(path[i]);
                }
                if(s0 * Math.exp(path[i]) < min_S || min_S < 0){
                    min_S = s0 * Math.exp(path[i]);
                }

                final_path.push({"x" : i*deltaT, "y" : s0 * Math.exp(path[i])});
            }
            new_paths.push(final_path);
        }
        
        setSMAX(max_S);
        setSMIN(min_S);
        setPaths(new_paths);
    };

    useEffect(()=> {
        getNewOptionPrice();
    }, [paths, optionPos, optionType, K]); 

    useEffect(() => {
        getNewPaths();
      }, [s0, mu, sigma, T, deltaT, num_paths,riskFreeRate]);
    
    const handleTypeInput = (e) => {
        setOptionType(e.target.value);
    };

    const handlePosInput = (e) => {
        setOptionPos(e.target.value);
    };

    const handleCurrencyInput = (e) => {
        if(e.target.value.length == 0)return;
        e.target.value = parseInt(100*e.target.value)/100;
        if(e.target.value < 0)
        {
            e.target.value = 0;
        }
        if(e.target.attributes.name.nodeValue === "s0"){
            setS0(parseFloat(e.target.value));
        }else if(e.target.attributes.name.nodeValue ===  "K"){
            setK(parseFloat(e.target.value));
        }
    };

    const handleVarInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.attributes.name.nodeValue ===  "mu"){
            setMu(parseFloat(e.target.value));
        }else if(e.target.attributes.name.nodeValue ===  "sigma"){
            setSigma(parseFloat(e.target.value));
        }
    };

    const handleTimeInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.value < 0)
        {
            e.target.value = 0;
        }
        if(e.target.value / deltaT > 1000)
        {
            e.target.value = 1000*deltaT
        }
        e.target.value = e.target.value;
        console.log(T);
        setT(parseFloat(e.target.value));
    };
    
    const handleDeltaTimeInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.value > T)
        {
            e.target.value = T;
        }
        if(e.target.value <= 0)
        {
            e.target.value = 0.1;
        }
        if(T / deltaT > 1000)
        {
            e.target.value = T/1000;
        }
        setDeltaT(parseFloat(e.target.value));
    };

    const handleNumPathsInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.value < 0)
        {
            e.target.value = 0;
        }else if(e.target.value > 500){
            e.target.value = 500;
        }
        setNumPaths(parseInt(e.target.value));
    };

    const handleRiskFreeInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.value < -1)
        {
            e.target.value = -1;
        }else if(e.target.value > 1){
            e.target.value = 1;
        }
        setRiskFreeRate(parseFloat(e.target.value));
    };

    useEffect(() => {
        const handleResize = () => {
          let f = .33
          if(window.innerWidth < 700)
          {
            setIsMobile(true);
            f = .6;
          }else{
            setIsMobile(false);
          }

          let w = f * window.innerWidth;
          if(w > 300)
          {
            setPlotWidth(300);
          }else
          {
            setPlotWidth(w);
          }
        };
        getNewPaths();
        handleResize();
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
      }, []);

      if(!isMobile)
      {
        return <div className="mc-option-container">
                <div className="mc-option-parent">
                    <div className="mc-option-container-left">
                        MC OPTION PRICER  <br></br> <br></br>
                        Option Style: <br></br>
                        <select onChange={handleTypeInput} name="option_type"> 
                             <option name="American" value="0" >American</option>
                             <option name="European" value="1" >European</option>
                             <option name="Asian" value="2" >Asian</option>
                        </select>  <br></br>  <br></br>
                        Option Type: <br></br>
                        <select onChange={handlePosInput} name="option_position"> 
                             <option name="Call" value="0" >Call</option>
                             <option name="Put" value="1" >Put</option>
                        </select>  <br></br>
                        <table className="mc-option-menu-table">
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="S(0)"/></a></td>
                            <td><input className="mc-option-pricer-number-input" onBlur={handleCurrencyInput} name="s0" type="number" placeholder="100" /></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="K"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleCurrencyInput} name="K" type="number" placeholder="101"/></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="r_{free}"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleRiskFreeInput} type="number" placeholder=".03"/></td>
                            </tr>
                            <tr> 
                            <td><a> <Tex2SVG display="inline" latex="\sigma"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleVarInput} name="sigma"  type="number" placeholder="0.05"/></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="T"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleTimeInput} type="number" placeholder="10"/></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="\Delta t"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleDeltaTimeInput} type="number"placeholder=".1"/></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="N_{paths}"/></a></td>
                            <td> <input className="mc-option-pricer-number-input"  onBlur={handleNumPathsInput} type="number"placeholder="100"/></td>
                            </tr>
                        </table>
                    </div>
                    <div className="mc-option-container-right">
                        <div className='mc-path-display'>
                        <Plot
    width={plotWidth}
    height={plotWidth}
    margin={{ bottom: 0, left: 0, top: 0, right: 0 }}
  >
    <Heading
      title="MC Option Pricer"
      subtitle="Stock Price vs Time"
    />
    {paths.map(path=> <LineSeries
      data={path}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'black', opacity: .05 }}
      label=""
      displayMarker={false}
    />)}
   
    <Axis
      id="y"
      position="left"
      label="Stock Price [$]"
      displayPrimaryGridLines
      min={sMIN}
      max={sMAX}
    />
    <Axis
      id="x"
      position="bottom"
      label="T"
      displayPrimaryGridLines
      min={0}
      max={T/1}
    />
  </Plot>
                        </div>
                        <div className="mc-option-pricer-result">
                            Option Price: {optionPrice}.
                        </div>
                    </div>
                </div>
           </div>
    }
    
    return <div className="mc-option-container-mobile">
    <div className="mc-option-parent-mobile">
        <div className="mc-option-container-top-mobile">
            <div className='mc-path-display-mobile'>
            <Plot
                width={plotWidth}
                height={plotWidth}
                margin={{ bottom: 0, left: 0, top: 0, right: 0 }}
                >
                <Heading
                title="MC Option Pricer"
                subtitle="Stock Price vs Time"
                />
                {paths.map(path=> <LineSeries
                data={path}
                xAxis="x"
                yAxis="y"
                lineStyle={{ stroke: 'black', opacity: .05 }}
                label=""
                displayMarker={false}
                />)}

                <Axis
                id="y"
                position="left"
                label="Stock Price [$]"
                displayPrimaryGridLines
                min={sMIN}
                max={sMAX}
                />
                <Axis
                id="x"
                position="bottom"
                label="T"
                displayPrimaryGridLines
                min={0}
                max={T/1}
                />
                </Plot>
            </div>
            <div className="mc-option-pricer-result">
                Option Price: {optionPrice}.
            </div>
        </div>
        <div className="mc-option-container-bottom-mobile">
            MC OPTION PRICER  <br></br> <br></br>
            Option Style: <br></br>
            <select onChange={handleTypeInput} name="option_type"> 
                 <option name="American" value="0" >American</option>
                 <option name="European" value="1" >European</option>
                 <option name="Asian" value="2" >Asian</option>
            </select>  <br></br>  <br></br>
            Option Type: <br></br>
            <select onChange={handlePosInput} name="option_position"> 
                 <option name="Call" value="0" >Call</option>
                 <option name="Put" value="1" >Put</option>
            </select>  <br></br>

            <table className="mc-option-menu-table">
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="S(0)"/></a></td>
                    <td><input  className="mc-option-pricer-number-input-mobile" onBlur={handleCurrencyInput} name="s0" type="number" placeholder="100" /></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="K"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleCurrencyInput} name="K" type="number" placeholder="101"/></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="r_{free}"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleRiskFreeInput} type="number" placeholder=".03"/></td>
                    </tr>
                    <tr> 
                    <td><a> <Tex2SVG display="inline" latex="\sigma"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleVarInput} name="sigma"  type="number" placeholder="0.05"/></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="T"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleTimeInput} type="number" placeholder="10"/></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="\Delta t"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleDeltaTimeInput} type="number"placeholder=".1"/></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="N_{paths}"/></a></td>
                    <td> <input className="mc-option-pricer-number-input-mobile" onBlur={handleNumPathsInput} type="number"placeholder="100"/></td>
                    </tr>
                </table>
        </div>
    </div>
</div>
};

export default MCOptionPricer;
