import React, { useState } from 'react';

const SubjectPage = ({ projects, project_jsx, project_index }) => {
    const [currentIndex, setCurrentIndex] = useState(project_index);


    const nextProject = (e) => {
        if(currentIndex >= projects.length-1)setCurrentIndex(0);
        else setCurrentIndex(prevIndex=>prevIndex+1);
    };
    const prevProject = (e) => {
        if(currentIndex <= 0)setCurrentIndex(projects.length-1);
        else setCurrentIndex(prevIndex=>prevIndex-1);
    };
    return <div>
            <div className="App-menu-container">
                <span onClick={prevProject} className="App-menu-option"> {'<'} PREV </span> 
                <span className="App-menu-option"> {projects[currentIndex]} </span>
                <span onClick={nextProject} className="App-menu-option"> {'>'} NEXT</span>
            </div>
            <div> {project_jsx[currentIndex]} </div>
        </div>

};

export default SubjectPage;