import React, { useState } from 'react';
import Tex2SVG from "react-hook-mathjax";
import MCOptionPricer from '../ProjectCode/MCOptionPricer';

const MCOptionPage = ({ }) => {

    return <div className="page-info-container">
            <MCOptionPricer />
            <h2>OPTIONS</h2>
            <div> <p className="page-info-block"> Options are a contract that give the owner the right to buy or sell a given asset at a specific price by a specific time. Options are sold for at the cost of a premium paid upfront - this allows the seller to profit if the option expires and recover from potential losses if the option is used. Due to their nature, vanilla options carry substantial risk for the seller and limit the risk for the buyer. The premium that is charged relies heavily on the conditions of the option contract and the model of the future stock prices.</p> </div>
            <div> 
                <p className="page-info-block">There are three main styles of vanilla options:
                <li><a className="custom-console-blue">American</a>: Option is based on current asset price can be used any time before it expires. </li>
                <li><a className="custom-console-blue">European</a>: Option is based on current asset price can only be used at expiration. </li>
                <li><a className="custom-console-blue">Asian</a>:    Option is based on average asset price and can be used any time before it expires.</li>
                </p>
            </div>
            <div> 
                <p className="page-info-block">There are two main types of option:
                <li><a className="custom-console-blue">Call</a>: You buy the right to buy the asset- betting the stock will increase even more than the premium. </li>
                <li><a className="custom-console-blue">Put</a>: You buy the right to sell the asset- betting the stock will decrease even more than the premium.</li>
                Naturally, when selling an option you need to invert the strategy. For example, selling a call option means you are betting the stock will not increase more than the premium. 
                </p>
            </div>
            <div> 
                <p className="page-info-block">
                The specific price the option is set to buy or sell at is called the <a className="custom-console-blue">strike price</a> and the specific time the option must be used by is called the <a className="custom-console-blue">expiration</a> or time to expiry.
                </p>
            </div>
            <h2>MODEL</h2>
            <div> <p className="page-info-block"> If we consider the puzzle of how to price a stock option, what we must first do is create a model to predict the motion of the stock price. From this model, we can hedge our premium against the risk associated with the option. 
            The model requires the following parameters:
            <li><Tex2SVG display="inline" latex="K"/> - Strike Price</li>
            <li><Tex2SVG display="inline" latex="\mu"/> - Stock Drift</li>
            <li><Tex2SVG display="inline" latex="\sigma"/> - Volatility</li>
            <li><Tex2SVG display="inline" latex="S_0"/> - Initial Stock Price</li>
            <li><Tex2SVG display="inline" latex="S(t)"/> - Stock Price at time t</li>
            <li><Tex2SVG display="inline" latex="T"/> - Time to expiry</li>
            <li><Tex2SVG display="inline" latex="\Delta t"/> - Time step</li>
            <li><Tex2SVG display="inline" latex="r_{free}"/> - Risk-free rate of return</li>
            </p>
             </div>
             <div> <p className="page-info-block"> The standard way to model the fluctuations of the stock price is through  <a className="custom-console-yellow">Stochastic Calculus</a>. Using the framework of stochastic calculus and our basic assumptions, we can build our model. 
             <li><a className="custom-console-yellow">Assumption #1</a>: Volatility is constant. </li>
             <li><a className="custom-console-yellow">Assumption #2</a>: Changes in stock price are randomly distributed. </li>
             <li><a className="custom-console-yellow">Assumption #3</a>: The market is arbitrage-free, allowing <Tex2SVG display="inline" latex="\mu = r_{free}"/>. </li>
             </p>
             </div>
             <div> <p className="page-info-block"> From these assumptions we can write the expression for the change in stock price,</p>
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="dS(t) = \mu S(t) dt + \sigma S(t) dW(t)"/>. </p>
             <p className="page-info-block"> Where <Tex2SVG display="inline" latex="dW(t)"/> is the Werner process and returns a random value normally distributed about zero with a standard deviation of <Tex2SVG display="inline" latex="dt"/>.</p>
             </div>
             <div> <p className="page-info-block"> Using this equation, we could jump right into programming the solution using a numerical method to solve the stochastic differential equation. However, this differential equatin has an exact solution which is famously known as <a className="custom-console-yellow">Geometric Brownian Motion</a>. </p>

             To solve it exactly, we must use Ito's lemma,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="d(\ln(F)) = \frac{dF}{F} - \frac{1}{2} \frac{dF dF}{F^2}"/>. </p>
             Where <Tex2SVG display="inline" latex="dS"/> is known from our equation above, so, <Tex2SVG display="inline" latex="dS dS"/> to first order of <Tex2SVG display="inline" latex="dt"/> becomes,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="dS dS = \sigma^2 S^2 dW^2 = \sigma^2 S^2 dt"/>.</p>
             Plugging this into Ito's lemma and substituting in the expression for <Tex2SVG display="inline" latex="dS/S"/>, we get,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="d(\ln(S)) = \mu - \frac{1}{2} \sigma^2 + \sigma dW"/>.</p>
             The final step is to integrate both sides and exponentiate equation,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="S(t) = S(0) e^{(\mu - \frac{\sigma^2}{2}) t + \sigma W(t)}"/>.</p>
             </div>
             <h2>MC PATHS</h2>
             <div> <p className="page-info-block"> Programming the model we derived above is actually quite a simple task. The Python3 function below takes in the parameters of our model and returns <a className="custom-color-red">num_paths</a> number of paths that are randomly generated.</p></div>
                <div className="page-code-container">
                &nbsp;import numpy as np<br></br>
                &nbsp;def get_paths(S0, mu, sigma, times, num_paths):<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; """<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : input float S0 : Inital Stock Price<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : input float mu : Average drift<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : input float sigma : Volatility<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : input float[] times : Array of time steps in time order<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : input int num_paths : Number of paths to generate<br></br><br></br>
                &nbsp;
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : return 2d numpy.array : 2D numpy array of shape(num_paths, len(times))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; """<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; init = np.tile([S0],(num_paths,1))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; stand_term = np.tile(np.diff(times) * (mu - 1/2 * sigma**2), (num_paths,1))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; stoch_term = sigma * np.random.normal(loc=0, scale=np.diff(times), size=(num_paths,len(times)-1))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; mc_step = stand_term + stoch_term<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp; return np.append(init, S0 * np.exp(np.cumsum(mc_step, axis=1)), axis=1)<br></br>
                </div>
                <div> <p className="page-info-block"> If we generate a large number of paths and plot them with some opacity for some artificially large input parameters, the structure of the geometric brownian motion reveals itself.</p>
                <div  className="page-img-block"><img className="page-img" width="100%" src={require("../MC_paths.png")}></img></div>


                <h2>PRICING</h2>
                <div> <p className="page-info-block"> Options should be priced based on their expected payout from our MC paths and based on the risk-free rate of return. The expected payout will differ between American, European, and Asian options but the methodology remains the same!</p>
                <p className="page-info-block"> If we choose to calculate for an Asian call option, the expected risk-adjusted payout is calculated as,
                </p>
                <p className="page-eq-center-block"><Tex2SVG display="block" latex="P = avg(max(0,\bar{S}-K)) e^{-rT}"/>. </p>
                Using this expected risk-adjusted payout as the price of the Asian call option, we reduce the risk while maintaining a competative option price.
                 </div>
                 <div className="page-code-container">
                 def calc_price_asian_call_option(paths, K, r, T):<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;avg_price_per_path = np.mean(paths, axis=1)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;return np.mean(np.where(avg_price_per_path-K {'<'} 0, 0, avg_price_per_path-K)) * np.exp(-r*T)<br></br>
                <br></br>
                 </div>
                 <h2>FULL CODE</h2>
                <div className="page-code-container-small-text">
                import numpy as np<br></br>
                import pandas as pd<br></br>
                from scipy.stats import t<br></br>
                from matplotlib import pyplot as plt<br></br>
                <br></br>
                def get_paths(S0, mu, sigma, times, num_paths):<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;"""<br></br>
                    : input float S0 : Inital Stock Price<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: input float mu : Average drift<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: input float sigma : Volatility<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: input float[] times : Array of time steps in time order<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: input int num_paths : Number of paths to generate<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: return 2d numpy.array : 2D numpy array of shape(num_paths, len(times))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;"""<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;init = np.tile([S0],(num_paths,1))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;stand_term = np.tile(np.diff(times) * (mu - 1/2 * sigma**2), (num_paths,1))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;stoch_term = sigma * np.random.normal(loc=0, scale=np.diff(times), size=(num_paths,len(times)-1))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;mc_step = stand_term + stoch_term<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;return np.append(init, S0 * np.exp(np.cumsum(mc_step, axis=1)), axis=1)<br></br>
                <br></br>
                def calc_limits(paths, confidence):<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;"""<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: input 2d numpy.array paths : 2D numpy array of shape(num_paths, len(times))<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: input float confidence : Confidence as a proportion from 0 to 1<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: return numpy.array1, numpy.array2 : array1 contains hi limits, array2 contains lo limits<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;"""<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;mean = np.mean(paths, axis=0)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;std = np.std(paths, axis=0)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;lo = t.ppf(1-confidence, len(paths), loc = mean, scale = std)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;hi = t.ppf(confidence, len(paths), loc = mean, scale = std)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;return mean, hi, lo<br></br>
                <br></br>
                def calc_price_asian_call_option(paths, K, r, T):<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;avg_price_per_path = np.mean(paths, axis=1)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;return np.mean(np.where(avg_price_per_path-K {'<'} 0, 0, avg_price_per_path-K)) * np.exp(-r*T)<br></br>
                <br></br>
                if __name__ == "__main__":<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Initialize variables<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;S0 = 100<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;mu = 0.1<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;sigma = 0.15<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;times = np.linspace(0,1,100)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;num_paths = 300<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;K = 101 <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;r = .005<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Calculate paths that follow Geometric Brownian Motion<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;paths = get_paths(S0, mu, sigma, times, num_paths)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Calculate price of asian call option<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;asian_call_price = calc_price_asian_call_option(paths, K, r, times[-1] - times[0])<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Calcualte hi and lo limits from generated paths<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;mean_path, hi_lim, lo_lim = calc_limits(paths, .95)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;textStr = "$N_{'{'}paths{'}'}=$ %d\n$S_{'{'}0{'}'}=$ %.2f\n$\mu$= %.4f\n$\sigma$ = %.4f" % (num_paths, S0, mu, sigma)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;fig, ax = plt.subplots()<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;props = dict(boxstyle='square', facecolor='white', alpha=1.0)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.set_title("Monte-Carlo Simulation | Stock Price vs. Time",loc="left", fontsize=12, pad=20)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.text(0.95, 0.05, textStr, transform=ax.transAxes, fontsize=10, verticalalignment='bottom', horizontalalignment='right', bbox=props)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Plot all paths as gray lines<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;for path in paths:<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;ax.plot(times, path, color="gray", alpha=.1)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Plot mean path as a red line<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.plot(times, mean_path, color="red", alpha=.5, label="$\overline {'{'}S_i{'}'}$")<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Plot hi and lo limits as blue dashed lines<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.plot(times, hi_lim, color="blue", linestyle="--", alpha=.5, label="95% CI")<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.plot(times, lo_lim, color="blue", linestyle="--", alpha=.5)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.set_ylabel("Stock Price")<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.set_xlabel("T")<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;ax.legend(prop = {'{'}"size"{':'} 12 \{'}'})<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;for item in ([ax.title, ax.xaxis.label, ax.yaxis.label] + ax.get_xticklabels() + ax.get_yticklabels()):<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;item.set_fontsize(12)<br></br>
                <br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;# Show plot<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;fig.set_size_inches(6,6)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;fig.savefig("MC_paths.png")
                </div>
                </div>
           </div>

};

export default MCOptionPage;