import React, { useState, useEffect } from 'react';

const RandomWriter = ({ text, num_rolls, delay, text_delay}) => {
  const [currentText, setCurrentText] = useState('');
  const [currentIndex, setCurrentIndex] = useState(0);
  const [currentRandom, setCurrentRandom] = useState('');
  const [currentTextIndex, setCurrentTextIndex] = useState(0);
  
  useEffect(() => {
    setCurrentText('');
    setCurrentIndex(0);
    setCurrentRandom('');
    setCurrentTextIndex(0);
  },[text]);

  const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
  useEffect(() => {
    let text_length = text[currentTextIndex].length; 
    if (currentIndex < num_rolls*(text_length+1)) {
        const timeout = setTimeout(() => {

            setCurrentText(text[currentTextIndex].substring(0,currentIndex/num_rolls));
            setCurrentIndex(prevIndex => prevIndex + 1);
            if(currentIndex < num_rolls*(text_length)) {
                setCurrentRandom(alphabet[Math.floor(Math.random() * alphabet.length)]);
            }else {
                setCurrentRandom('');
            }
        }, delay);

        return () => {clearTimeout(timeout);}
    }else{
        const timeout = setTimeout(() => {
            if(currentTextIndex +1 >= text.length)
            {
                if( text.length > 1){
                    setCurrentTextIndex(0);
                    setCurrentIndex(0);
                    setCurrentText(text[currentTextIndex][0]);
                }
            }else {
                setCurrentTextIndex(prevTextIndex => prevTextIndex + 1);
                setCurrentIndex(0);
                setCurrentText(text[currentTextIndex][0]);
            }
        }, text_delay);

        return () => {clearTimeout(timeout);}
    }
  }, [currentIndex, currentTextIndex, delay, num_rolls, text, text_delay]);

  return <span>{currentText}<span className='random-letter'>{currentRandom}</span></span>;
};

export default RandomWriter;